
#contain{
    width:100%;
    /* height:100vh; */
    background-color: rgba(24,32,57,255);
    font-family: 'Share Tech', sans-serif;
}
* {
    -ms-overflow-style: none;  /* Internet Explorer 10+ */
    scrollbar-width: none;  /* Firefox */
}
*::-webkit-scrollbar { 
    display: none;  /* Safari and Chrome */
}
#card{
    padding:2vw;
    width:40vw;
    min-width:300px;
    
    background-color: rgba(33,43,78,255);
    box-shadow: 5px 5px rgba(54, 63, 99, 0.788);
}
#cardMain{
    width:100%;
    /* height:40vh; */
    padding:1.5vh;
    text-align: center;
    background-color: rgba(33,43,78,255);
    box-shadow: 5px 5px rgba(54, 63, 99, 0.788);
    color:white;
}
#content{
    position: absolute;
    left: 50%;
    top: 40%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}
ul li a {
    text-decoration: none;
  
}
@media only screen and (min-width: 700px) {
    #card{
        max-width:40vh;
    }
  }
